
#preview {
  background: #FFF;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.25);
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  padding: 2em;
  margin-left: 16px;
  overflow: auto;
  color: #333;
  line-height: 1.2;
}
#preview * {
    box-sizing: border-box;
    font-variant: normal;
    font-weight: normal;
}
#preview ol {
    list-style: none;
}
#preview section + section {
    margin-top: 2em;
}
#preview p {
    white-space: pre-line;
}
#preview section[data-name="profile"] > h1 {
    margin: .1em 0;
    font-size: 4em;
}
#preview section[data-name="workHistory"] li,
  #preview section[data-name="projects"] li,
  #preview section[data-name="awards"] li {
    margin-top: 1em;
}
#preview section[data-name="workHistory"] li h3,
  #preview section[data-name="projects"] li h3,
  #preview section[data-name="awards"] li h3 {
    border-bottom: 1px solid #999;
    padding-bottom: 0.3em;
    margin-bottom: .3em;
}
#preview section[data-name="education"] li {
    line-height: 1.5;
}
#preview section[data-name="contacts"] td:first-child {
    padding-right: 1em;
}



